.video {
  position: relative;
}

.video__button {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  box-sizing: border-box;
  width: 100px;
  height: 76px;

  background-color: $color-light-accent;
  border: 5px solid #ffffff;
  border-radius: 25px;
  background-image: url("../img/icon/process-arrow.svg");
  background-repeat: no-repeat;
  background-position: center;

  cursor: pointer;
}

.video__image {
  display: block;
  margin: 0;
}

@media (min-width: 768px) {
  .video__image {
    width: 668px;
    height: 455px;
  }

  .video__button {
    &:hover {
      transform: translate(-50%, -50%) scale(1.1);
    }

    &:active {
      transform: translate(-50%, -50%) scale(0.9);
    }

    &:active::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      z-index: 100;
      box-sizing: border-box;
      width: 100%;
      height: 100%;
      background-color: #ffffff;
      opacity: 0.3;
      border-radius: 20px;
    }
  }
}

@media (min-width: 1150px) {
  .video__image {
    width: 683px;
    height: 455px;
  }
}
